* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: hsl(212, 45%, 89%);
}

.wraper {
  display: grid;
  background-color: white;
  border: 1px solid white;
  border-radius: 8px;
  width: 400px;
  height: 600px;
  position: absolute;
  left: 250px;
  top: 100px;
}

p {
  font-family: sans-serif;
  width: auto;
}

P:last-child {
  font-size: 15px;
  color: #777;
}

.img-qr {
  width: 350px;
  height: 300px;
  border-radius: 6px;
}

.image-div {
  display: block;
  align-items: center;
  margin: 10px auto;
}
